import React from "react";
import PropType from "prop-types";

function Iframe({ url }) {
  return (
    <iframe
      src={url}
      width="640"
      height="736"
      frameborder="0"
      marginheight="0"
      marginwidth="0"
    >
      Loading...
    </iframe>
  );
}

Iframe.PropType = {
  url: PropType.string.isRequired
};

export default Iframe;
