import React, { Component } from "react";
import "./App.css";
import Blog from "./blog";
import ModalForm from "./components/modalForm";
import form from "./constants/forms";
import { string } from "prop-types";
import post1 from "./blog-post.1.md";

const toggleKey = key => state => {
  return { [key]: !state[key] };
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showModel: true,
      url: form()
    };
  }

  handleToggle = key => {
    this.setState(toggleKey(key));
  };

  handleform = type => {
    this.setState({ url: form(type) }, () => this.handleToggle("showModel"));
  };

  componentDidMount() {
    fetch(post1)
      .then(response => response.text())
      .then(text => this.setState({ text: text }));
  }

  render() {
    const modalFormProps = {
      url: this.state.url,
      open: this.state.showModel,
      handleChange: () => this.handleToggle("showModel")
    };

    return (
      <React.Fragment>
        <Blog handleform={type => this.handleform(type)} {...this.state} />
        <ModalForm {...modalFormProps} />
      </React.Fragment>
    );
  }
}

export default App;
