export const subscribe =
  "https://docs.google.com/forms/d/e/1FAIpQLSesulxRiLNJ7u1jhNiaD4VhD6dvnuo91r3dYH6GAOkaEG_GJA/viewform?embedded=true";

export const volunteer =
  "https://docs.google.com/forms/d/e/1FAIpQLSdWTq-13opFS9gWanNV9TiXYPen34kjp7G-S_0wo8BjRDAVFQ/viewform?embedded=true";

export const signUp =
  "https://docs.google.com/forms/d/e/1FAIpQLSfOz0mBNwKjbGA7VbftcbpTIluDJ15dVcEnqk6THtu867RHcw/viewform?embedded=true";

export default function form(type) {
  switch (type) {
    case "subscribe":
      return subscribe;
    case "volunteer":
      return volunteer;
    case "signUp":
      return signUp;
    default:
      return subscribe;
  }
}
